<template>
  <div>
    <div v-if="!showCfDnsRecords">
      <el-button type="success"
                 style="margin-bottom: 20px;"
                 @click="getCfCfgList">
        刷新
      </el-button>
      <el-input
          v-model="pageParams.keyword"
          placeholder="模糊搜索"
          @input="getCfCfgList"
          clearable prefix-icon="Search"
          style="margin-left: 20px; width: 400px; margin-bottom: 20px;"
      />
      <el-button
          type="primary"
          @click="addCfCfgDialog = true"
          style="margin-left: 20px; margin-bottom: 20px;"
      >
        新增配置
      </el-button>
      <el-button
          type="danger"
          @click="batchRemove"
          :disabled="!batchSelected.length"
          style="margin-left: 20px; margin-bottom: 20px;"
      >
        批量删除
      </el-button>
      <!-- 可滑动的表格 -->
      <div style="overflow-x: auto;">
        <el-table
            :data="cfCfgPageRsp"
            style="width: 100%; min-width: 800px;"
            v-loading="loading"
            @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="35"/>
          <el-table-column label="域名" width="300">
            <template #default="{ row }">
              <el-tooltip :content="row.domain" placement="top">
                <el-tag type="success">
                  {{ row.domain }}
                </el-tag>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="ZoneId" width="350">
            <template #default="{ row }">
              <el-tooltip :content="row.zoneId" placement="top">
                {{ row.zoneId }}
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="Token" width="400">
            <template #default="{ row }">
              <el-tooltip :content="row.apiToken" placement="top">
                {{ row.apiToken }}
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="创建时间">
            <template #default="{ row }">
              <el-tooltip :content="row.createTime" placement="top">
                <el-tag type="info">
                  {{ row.createTime }}
                </el-tag>
              </el-tooltip>
            </template>
          </el-table-column>

          <el-table-column label="操作" width="220">
            <template #default="{ row }">
              <el-button @click="handleUpdateCfCfg(row)" type="primary" link>
                编辑
              </el-button>
              <el-button @click="handleCfDnsRecords(row)" type="success" link>
                DNS记录
              </el-button>
              <el-button @click="handleRemove(row)" type="danger" link>
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- 分页控制 -->
      <el-pagination
          @current-change="handlePageChange"
          @size-change="handleSizeChange"
          v-model:current-page="pageParams.currentPage"
          v-model:page-size="pageParams.pageSize"
          :page-sizes="[5, 10, 20, 50]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          style="margin-top: 20px;"
      />
    </div>

    <div v-else>
      <el-button @click="showCfDnsRecords = false">返回 Cloudflare 配置列表</el-button>
      <CloudflareDnsRecords :cfCfgId="cfCfgId" :cfCfgName="cfCfgName"/>
    </div>
  </div>

  <el-dialog
      v-model="addCfCfgDialog"
      title="新增 Cloudflare 配置名称"
      width="40%"
      @close="addCfCfgDialog = false"
  >
    <el-form label-width="100px">
      <el-form-item label="域名" label-width="130">
        <el-input
            class="cf-input"
            v-model="AddCfCfgParams.domain"
            placeholder="请输入托管于 Cloudflare 的域名，例：abc.xyz"
            clearable
        />
      </el-form-item>

      <el-form-item label="区域ID (ZoneId)" label-width="130">
        <el-input
            class="cf-input"
            v-model="AddCfCfgParams.zoneId"
            placeholder="请输入托管于 Cloudflare 的域名的区域ID"
            clearable
        />
      </el-form-item>

      <el-form-item label="API令牌 (Token)" label-width="130">
        <el-input
            class="cf-input"
            v-model="AddCfCfgParams.apiToken"
            placeholder="请输入托管于 Cloudflare 的域名的 API令牌（需要自行生成）"
            clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="addCfCfgDialog = false">取消</el-button>
        <el-button type="primary" @click="addCfCfg()" :loading="addCfCfgLoading">新增</el-button>
      </div>
    </template>
  </el-dialog>


  <el-dialog
      v-model="updateCfCfgDialog"
      title="修改 Cloudflare 配置名称"
      width="40%"
      @close="updateCfCfgDialog = false"
  >
    <el-form label-width="100px">
      <el-form-item label="域名"  label-width="130">
        <el-input
            class="cf-input"
            v-model="UpdateCfCfgParams.domain"
            placeholder="请输入托管于 Cloudflare 的域名，例：abc.xyz"
            clearable
        />
      </el-form-item>

      <el-form-item label="区域ID (ZoneId)"  label-width="130">
        <el-input
            class="cf-input"
            v-model="UpdateCfCfgParams.zoneId"
            placeholder="请输入托管于 Cloudflare 的域名的区域ID"
            clearable
        />
      </el-form-item>

      <el-form-item label="API令牌 (Token)"  label-width="130">
        <el-input
            class="cf-input"
            v-model="UpdateCfCfgParams.apiToken"
            placeholder="请输入托管于 Cloudflare 的域名的 API令牌（需要自行生成）"
            clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="updateCfCfgDialog = false">取消</el-button>
        <el-button type="primary" @click="updateCfCfg()" :loading="updateCfCfgLoading">修改</el-button>
      </div>
    </template>
  </el-dialog>

</template>

<script>
import CloudflareDnsRecords from "@/components/syscfg/CloudflareDnsRecords.vue";
import {confirmDialog} from "@/js/common";
import {ElMessage} from "element-plus";

export default {
  components: {CloudflareDnsRecords},
  mounted() {
    this.getCfCfgList()
  },
  data() {
    return {
      addCfCfgDialog: false,
      addCfCfgLoading: false,
      updateCfCfgDialog: false,
      updateCfCfgLoading: false,
      showCfDnsRecords: false,
      cfCfgId: null,
      cfCfgName: '',
      loading: true,
      pageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 10,
      },
      cfCfgPageRsp: [
        {
          id: '',
          domain: '',
          zoneId: '',
          apiToken: '',
          createTime: ''
        }
      ],
      total: 0,
      batchSelected: [],
      removeIds: [],
      AddCfCfgParams: {
        domain: '',
        zoneId: '',
        apiToken: '',
      },
      UpdateCfCfgParams: {
        id: '',
        domain: '',
        zoneId: '',
        apiToken: '',
      },
    };
  },
  methods: {
    async getCfCfgList() {
      this.loading = true
      await this.$axios.post('/cf/listCfg', {...this.pageParams})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: '获取Cloudflare配置成功',
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.cfCfgPageRsp = response.data.data.records;
              this.total = response.data.data.total;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
          })
    },
    async batchRemove() {
      const confirmed = await confirmDialog('确定要删除配置吗？', '删除 Cloudflare 配置');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.loading = true
      this.removeIds = this.batchSelected.map(record => record.id);
      await this.$axios.post('/cf/removeBatch', {idList: this.removeIds})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '删除 Cloudflare 配置成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.getCfCfgList();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async addCfCfg() {
      const confirmed = await confirmDialog('确定要新增配置吗？', '新增 Cloudflare 配置');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.addCfCfgLoading = true
      await this.$axios.post('/cf/add', {...this.AddCfCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '新增 Cloudflare 配置成功',
                type: 'success',
                duration: 2000
              });
              this.addCfCfgDialog = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.getCfCfgList();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateCfCfg() {
      const confirmed = await confirmDialog('确定要修改配置吗？', '修改 Cloudflare 配置');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.updateCfCfgLoading = true
      await this.$axios.post('/cf/update', {...this.UpdateCfCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '修改 Cloudflare 配置成功',
                type: 'success',
                duration: 2000
              });
              this.updateCfCfgDialog = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.getCfCfgList();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleUpdateCfCfg(row) {
      this.UpdateCfCfgParams.id = row.id
      this.UpdateCfCfgParams.domain = row.domain
      this.UpdateCfCfgParams.zoneId = row.zoneId
      this.UpdateCfCfgParams.apiToken = row.apiToken
      this.updateCfCfgDialog = true
    },
    cleanCache() {
      this.batchSelected = []
      this.AddCfCfgParams = {}
      this.loading = false
      this.addCfCfgLoading = false
      this.updateCfCfgLoading = false
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    handleCfDnsRecords(row) {
      this.cfCfgId = row.id
      this.cfCfgName = row.domain
      this.showCfDnsRecords = true
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getCfCfgList();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getCfCfgList();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

<style scoped>
.cf-input {
  margin-bottom: 16px;
}

.el-form-item {
  margin-bottom: 16px;
}
</style>