<template>
  <span style="font-size: 16px; font-weight: bold; margin-left: 20px">当前配置：{{ this.cfCfgName }}</span>
  <el-divider class="divider"/>

  <el-button type="success"
             style="margin-bottom: 20px;"
             @click="reloadCfDnsRecords">
    刷新
  </el-button>
  <el-input
      v-model="pageParams.keyword"
      placeholder="模糊搜索"
      @input="getCfDnsRecords"
      clearable prefix-icon="Search"
      style="margin-left: 20px; width: 400px; margin-bottom: 20px;"
  />
  <el-button
      type="primary"
      @click="addDnsRecordDialog = true"
      style="margin-left: 20px; margin-bottom: 20px;"
  >
    新增 DNS 记录
  </el-button>
  <el-button
      type="danger"
      @click="batchRemove"
      :disabled="!batchSelected.length"
      style="margin-left: 20px; margin-bottom: 20px;"
  >
    批量删除
  </el-button>
  <!-- 可滑动的表格 -->
  <div style="overflow-x: auto;">
    <el-table
        :data="cfDnsRecordsPageRsp"
        style="width: 100%; min-width: 800px;"
        v-loading="loading"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="35"/>
      <el-table-column label="域名" width="300">
        <template #default="{ row }">
          <el-tooltip :content="row.name" placement="top">
            <el-tag type="success">
              {{ row.name }}
            </el-tag>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="类型" width="100">
        <template #default="{ row }">
          {{ row.type }}
        </template>
      </el-table-column>
      <el-table-column label="内容" width="300">
        <template #default="{ row }">
          <el-tooltip :content="row.content" placement="top">
            {{ row.content }}
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="开启CDN" width="100">
        <template #default="{ row }">
          <el-tooltip :content="row.proxied === true ? '是' : '否'" placement="top">
            <el-tag :type="row.proxied === true ? 'success':'info'">
              {{ row.proxied === true ? "是" : "否" }}
            </el-tag>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="TTL（秒）" width="100">
        <template #default="{ row }">
          {{ row.ttl === 1 ? '自动' : row.ttl }}
        </template>
      </el-table-column>
      <el-table-column label="备注">
        <template #default="{ row }">
          <el-tooltip :content="row.comment" placement="top">
            {{ row.comment }}
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="创建时间">
        <template #default="{ row }">
          <el-tooltip :content="row.createdOn" placement="top">
            <el-tag type="info">
              {{ row.createdOn }}
            </el-tag>
          </el-tooltip>
        </template>
      </el-table-column>

      <el-table-column label="操作" width="220">
        <template #default="{ row }">
          <el-button @click="handleUpdateDns(row)" type="primary" link>
            编辑
          </el-button>
          <el-button @click="handleRemove(row)" type="danger" link>
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>

  <el-dialog
      v-model="addDnsRecordDialog"
      title="新增 Cloudflare DNS 记录"
      width="40%"
      @close="addDnsRecordDialog = false"
  >
    <el-form label-width="100px">
      <el-form-item label="记录类型">
        <el-select v-model="OciAddCfDnsRecordsParams.type" placeholder="请选择记录类型">
          <el-option label="A (IPv4)" value="A"/>
          <el-option label="AAAA (IPv6)" value="AAAA"/>
          <el-option label="CNAME (别名)" value="CNAME"/>
        </el-select>
      </el-form-item>

      <el-form-item label="域名前缀">
        <el-input
            v-model="OciAddCfDnsRecordsParams.prefix"
            placeholder="请输入域名前缀，例如 oracle-kr"
            clearable
        />
      </el-form-item>

      <el-form-item v-if="OciAddCfDnsRecordsParams.type === 'A' || OciAddCfDnsRecordsParams.type === 'AAAA'"
                    label="IP 地址">
        <el-input
            v-model="OciAddCfDnsRecordsParams.ipAddress"
            placeholder="请输入 IP 地址"
            clearable
        />
      </el-form-item>

      <el-form-item v-if="OciAddCfDnsRecordsParams.type === 'CNAME'" label="目标域名">
        <el-input
            v-model="OciAddCfDnsRecordsParams.ipAddress"
            placeholder="请输入目标域名，例如 target.example.com"
            clearable
        />
      </el-form-item>

      <el-form-item label="代理加速">
        <el-switch v-model="OciAddCfDnsRecordsParams.proxied" active-text="开启" inactive-text="关闭"/>
      </el-form-item>

      <el-form-item label="TTL（秒）">
        <el-input-number
            v-model="OciAddCfDnsRecordsParams.ttl"
            :min="60"
            :step="60"
            placeholder="TTL 秒数"
        />
      </el-form-item>

      <el-form-item label="备注">
        <el-input
            type="textarea"
            v-model="OciAddCfDnsRecordsParams.comment"
            placeholder="可填写备注"
            rows="2"
            clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="addDnsRecordDialog = false">取消</el-button>
        <el-button type="primary" @click="addCfDnsRecords" :loading="addDnsRecordLoading">
          新增
        </el-button>
      </div>
    </template>
  </el-dialog>

  <el-dialog
      v-model="updateDnsRecordDialog"
      title="更新 Cloudflare DNS 记录"
      width="40%"
      @close="updateDnsRecordDialog = false"
  >
    <el-form label-width="100px">
      <el-form-item label="记录类型">
        <el-select v-model="OciUpdateCfDnsRecordsParams.type" placeholder="请选择记录类型" disabled>
          <el-option label="A (IPv4)" value="A"/>
          <el-option label="AAAA (IPv6)" value="AAAA"/>
          <el-option label="CNAME (别名)" value="CNAME"/>
        </el-select>
      </el-form-item>

      <el-form-item label="域名前缀">
        <el-input
            v-model="OciUpdateCfDnsRecordsParams.prefix"
            placeholder="请输入域名前缀"
            clearable
        />
      </el-form-item>

      <el-form-item
          v-if="OciUpdateCfDnsRecordsParams.type === 'A' || OciUpdateCfDnsRecordsParams.type === 'AAAA'"
          label="IP 地址"
      >
        <el-input
            v-model="OciUpdateCfDnsRecordsParams.ipAddress"
            placeholder="请输入 IP 地址"
            clearable
        />
      </el-form-item>

      <el-form-item v-if="OciUpdateCfDnsRecordsParams.type === 'CNAME'" label="目标域名">
        <el-input
            v-model="OciUpdateCfDnsRecordsParams.ipAddress"
            placeholder="请输入目标域名"
            clearable
        />
      </el-form-item>

      <el-form-item label="代理加速">
        <el-switch
            v-model="OciUpdateCfDnsRecordsParams.proxied"
            active-text="开启"
            inactive-text="关闭"
        />
      </el-form-item>

      <el-form-item label="TTL（秒）">
        <el-input-number
            v-model="OciUpdateCfDnsRecordsParams.ttl"
            :min="60"
            :step="60"
            placeholder="TTL 秒数"
        />
      </el-form-item>

      <el-form-item label="备注">
        <el-input
            type="textarea"
            v-model="OciUpdateCfDnsRecordsParams.comment"
            placeholder="备注信息"
            rows="2"
            clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="updateDnsRecordDialog = false">取消</el-button>
        <el-button type="primary" @click="updateCfDnsRecord" :loading="updateDnsRecordLoading">
          更新
        </el-button>
      </div>
    </template>
  </el-dialog>


</template>

<script>
import {ElMessage} from "element-plus";
import {confirmDialog} from "@/js/common";

export default {
  props: {
    cfCfgId: String,
    cfCfgName: String,
  },
  mounted() {
    this.pageParams.cfCfgId = this.cfCfgId
    this.getCfDnsRecords()
  },
  data() {
    return {
      loading: true,
      addDnsRecordDialog: false,
      addDnsRecordLoading: false,
      updateDnsRecordDialog: false,
      updateDnsRecordLoading: false,
      pageParams: {
        cfCfgId: '',
        keyword: '',
        currentPage: 1,
        pageSize: 10,
        cleanReLaunch: false
      },
      cfDnsRecordsPageRsp: [
        {
          id: '',
          name: '',
          type: '',
          content: '',
          proxiable: null,
          proxied: null,
          ttl: null,
          comment: '',
          createdOn: '',
          modifiedOn: '',
          tags: []
        }
      ],
      total: 0,
      batchSelected: [],
      removeIds: [],
      OciAddCfDnsRecordsParams: {
        cfCfgId: '',
        prefix: '',
        type: '',
        ipAddress: '',
        proxied: false,
        ttl: 60,
        comment: ''
      },
      OciUpdateCfDnsRecordsParams: {
        cfCfgId: '',
        id: '',
        prefix: '',
        type: '',
        ipAddress: '',
        proxied: false,
        ttl: 60,
        comment: ''
      },
    }
  },
  methods: {
    async reloadCfDnsRecords() {
      this.pageParams.cleanReLaunch = true
      this.loading = true
      this.getCfDnsRecords()
      this.pageParams.cleanReLaunch = false
    },
    async getCfDnsRecords() {
      this.loading = true
      await this.$axios.post('/cf/listCfDnsRecord', {...this.pageParams})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: '获取Cloudflare配置成功',
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.cfDnsRecordsPageRsp = response.data.data.records;
              this.total = response.data.data.total;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
          })
    },
    async batchRemove() {
      const confirmed = await confirmDialog('确定要删除 DNS 记录吗？', '删除 Cloudflare DNS 记录');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.loading = true
      this.removeIds = this.batchSelected.map(record => record.id);
      await this.$axios.post('/cf/removeCfDnsRecord', {recordIds: this.removeIds, cfCfgId: this.cfCfgId})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '删除 Cloudflare DNS 记录成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.reloadCfDnsRecords();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async addCfDnsRecords() {
      const confirmed = await confirmDialog('确定要新增 DNS 记录吗？', '新增 Cloudflare DNS 记录');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.OciAddCfDnsRecordsParams.cfCfgId = this.cfCfgId
      this.addDnsRecordLoading = true
      await this.$axios.post('/cf/addCfDnsRecord', {...this.OciAddCfDnsRecordsParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '新增 Cloudflare DNS 记录成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.reloadCfDnsRecords();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateCfDnsRecord() {
      const confirmed = await confirmDialog('确定要更新 DNS 记录吗？', '更新 Cloudflare DNS 记录');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.OciUpdateCfDnsRecordsParams.cfCfgId = this.cfCfgId
      this.updateDnsRecordLoading = true
      await this.$axios.post('/cf/updateCfDnsRecord', {...this.OciUpdateCfDnsRecordsParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '更新 Cloudflare DNS 记录成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
            this.reloadCfDnsRecords();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleUpdateDns(row) {
      this.OciUpdateCfDnsRecordsParams.id = row.id
      this.OciUpdateCfDnsRecordsParams.type = row.type
      this.OciUpdateCfDnsRecordsParams.ipAddress = row.content
      this.OciUpdateCfDnsRecordsParams.proxied = row.proxied
      this.OciUpdateCfDnsRecordsParams.ttl = row.ttl
      this.OciUpdateCfDnsRecordsParams.comment = row.comment

      const domain = row.name
      if (domain.endsWith(this.cfCfgName)) {
        const suffix = '.' + this.cfCfgName;
        this.OciUpdateCfDnsRecordsParams.prefix = domain.slice(0, -suffix.length)
      }

      this.updateDnsRecordDialog = true
    },
    cleanCache() {
      this.batchSelected = []
      this.loading = false
      this.addDnsRecordLoading = false
      this.addDnsRecordDialog = false
      this.updateDnsRecordLoading = false
      this.updateDnsRecordDialog = false
      this.OciAddCfDnsRecordsParams = {}
      this.OciUpdateCfDnsRecordsParams = {}
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getCfDnsRecords();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getCfDnsRecords();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

<style scoped>
.cf-input {
  margin-bottom: 16px;
}
</style>